import Vue from 'vue';
// import Router from 'vue-router';
import VueRouter from 'vue-router';


import nav from '@/components/nav';
// import chatpage from '@/components/chatpage';
import footer from '@/components/footer';
import login from '@/components/login';
import home from '@/components/home';
// import register from '@/components/register';
// import tp1 from '@/components/tp1';
// import message from '@/components/message';
// import user from '@/components/user';
// import nlogin from '@/components/nlogin';
import content from '@/components/content';
import changepswpage from '@/components/changepsw';

// 注册全局头组件
Vue.component('navVue',nav);
Vue.component('footerVue',footer);
// Vue.component('message',message);
// Vue.component('user',user);
// Vue.component('nlogin',nlogin);
// 安装插件
Vue.use(VueRouter);//挂载属性



export default new VueRouter({
    routes:[
        {
            path:'/',
            // redirect:{name:'testchatpage'},
            // redirect:{name:'login'},
            redirect:{name:'home'},
        },
        {
            name:'home',
            path:'/home',
            component:home,
            children:[
                // 这里很灵活，如果你写上 /xxx 就是绝对路径 /oumei
                // 如果你不写/,那么就是相对路径 /music/oumei
                {name:'content',path:'content',component:content},
                // 标志一下当前路由之间的关系，格式不是必须的
                // {name:'content2',path:'content2',component:content2}
            ]
        } ,
        {
            name:'login',
            path:'/login',
            component:login,
            children:[
                // 这里很灵活，如果你写上 /xxx 就是绝对路径 /oumei
                // 如果你不写/,那么就是相对路径 /music/oumei
                {name:'content',path:'content',component:content},
                // 标志一下当前路由之间的关系，格式不是必须的
                // {name:'content2',path:'content2',component:content2}
            ]
        } ,
        // {
        //   name:'register',
        //   path: '/register',
        //   component: register
        // },
        // {
        //     name:'chatpage',
        //     path: '/chatpage',
        //     component: chatpage
        // },
        {
            name:'testpage',
            path: '/testpage',
            component: content
        },
        {
            name:'changepswpage',
            path: '/changepswpage',
            component: changepswpage
        },
        { path: '*', redirect: '/', hidden: true }
    ]
});


