<script>
import {mapState} from 'vuex'
import {setCookie} from '../assets/js/setcookie.js'
import axios from 'axios'
import crypto from 'crypto'

export default {

  computed:mapState({
      user_name:state => state.user_name
  }),
  data(){
      return {
        // 屏幕高度
        // clientHeight:'',
        // clientWidth:{val:0},
        clientWidth:document.body.clientWidth, 
        screenWidth: document.body.clientWidth, // 屏幕尺寸

        userName: '',
        pwd: '',
        // 辅助功能
        classbg:'bg',
        classFade:'',
        notice_info:'用戶名或者密碼錯誤或者不存在！',
        info_hide:'hide',
        direct:'direct',
        pl2:'pl2',

        //  用于表单下划线样式的变换
        hrName: false,
        hrPassword: false,
        hr_name_current:{
          transform: 'scaleX(1)'
        },
        hr_password_current:{
          transform: 'scaleX(1)'
        }
        //  用于表单下划线样式的变换
      }
  },
  mounted:function(){
    var self = this;
      // 获取浏览器可视区域高度
      // this.clientHeight =   `${document.documentElement.clientHeight}`  
      // 宽度
      // this.clientWidth =   `${document.documentElement.clientWidth}`  

      window.onresize = function() {
        // this.clientHeight = `${document.documentElement.clientHeight}`;
        self.clientWidth =   `${document.documentElement.clientWidth}`
        
        console.log("mounted"+self.clientWidth)
        console.log("getit")
      };
    },
    watch: {
      
      clientWidth:{
        handler:function (cval, oval) {
          
          console.log(this.clientWidth)
          console.log("watch"+this.clientWidth)
          // this.changeFixed(this.clientWidth.val)
          this.changeFixed(cval)
        },
        deep: true,
        // immediate: true
      } 
    },
   methods:{
    changeFixed(clientWidth){  //动态修改样式
      console.log(clientWidth);
      // this.$refs.homePage.style.width = clientWidth+'px';

      this.$refs.homePage.style.height = clientWidth*0.5625+'px';
      // this.$refs.homePage.style.width = clientWidth+'px';
      this.$refs.slogan_1.style.fontSize = clientWidth*0.0018+'em';
      this.$refs.slogan_2.style.fontSize = clientWidth*0.0018+'em';
      this.$refs.slogan_3.style.fontSize = clientWidth*0.0042+'em';
      this.$refs.slogandiv.style.marginTop = clientWidth*0.15+'px';
    },

    //  用于表单下划线样式的变换
     change_hr_name() {
       this.hrName = true;
     },
     change_hr_password() {
       this.hrPassword = true;
     },
     reset_hr_name:function() {
      this.hrName = false;
     },
     reset_hr_password:function() {
       this.hrPassword = false;
     },
     //  用于表单下划线样式的变换
    
    //  getmd5(code){
    //         return crypto.createHash('md5').update(code).digest('hex');
    //   },
     closemodel: function() {
                this.classFade = 'hide'
            },
     checkUser() {
        var name = this.userName;
        var pwd = crypto.createHash('md5').update(this.pwd).digest('hex');
        console.log(name+":"+pwd);
        var self = this; //很关键
        if(name != '' && pwd != '') {
          axios({
              method: 'post',
              url: 'user/searchUser',
              data: {
                username: name,
                pwd: pwd
						},
						timeout: 3000
                    }).then(function(response) {
                        // console.log(response.status)
                        var i, flag;
                        // for(i in response.data) {
                        // if((name == response.data[0].UserName && pwd == response.data[i].PassWord && name != '' && pwd != '')) {
                          console.log(response.data);
                          console.log(response.data[0]);
                          if(response.data==200){
                                flag = 'allright';
                            }
                        // }
                        if(flag == 'allright') {
                            var cookie_name = unescape(setCookie.setInfo().name);
                            if((cookie_name != name && name != '' && cookie_name != '') || cookie_name == '') {
                                setCookie.getInfo(self.userName, pwd);
                                setCookie.userLogin();
                                // self.classFade = '';
                                // self.info_hide = '';
                                // self.notice_info = '登錄成功！'
                                self.$router.push('/testpage')
                            } else if(cookie_name == name && name != '' && cookie_name != '') {
                                // self.classFade = '';
                                // self.info_hide = '';
                                // self.notice_info = '登錄成功！'
                                self.$router.push('/testpage')
                            }
                        } else {
                            // self.classFade = ''
                            // self.$router.push('/testpage')
                            alert("密码不正确");
                        }
                    }).catch(function(error) {
                        console.log(error);
                        // self.classFade = '';
                        // self.notice_info = '服務器繁忙，請刷新頁面或者稍後重試!(Error code: 504)'
                    });
                }
              else{
                alert("账号密码不能为空！");
                  // self.classFade = ''
              }
            }
  },
}
</script>
<style lang='scss' scoped>
// @import '../assets/image/3dprinter/bg.jpg';

// .c_navbar {
