  // background-size: cover;
  
}
form {
  width: 30%;
  margin: 0 auto;
.inp_name_div {
    font-size: 22px;
    line-height: 24px;
    width: 100%;
    height: 72px;
    display: inline-block;
    position: relative;
    background-color: transparent;
    font-family: Roboto, sans-serif;
    transition: height 200ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    .username_id_lab {
      position: absolute;
      line-height: 22px;
      // top: 38px;
      top: 63px;
      left: -45px;
      transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
      z-index: 1;
      cursor: text;
      transform: scale(0.75) translate(0px, -28px);
      transform-origin: left top 0px;
      pointer-events: none;
      user-select: none;
      color: rgba(0, 0, 0, 0.5);
    }
    .inp_name {
      -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
      padding: 0px;
      position: relative;
      width: 100%;
      border: none;
      outline: none;
      background-color: rgba(0, 0, 0, 0);
      color: rgba(0, 0, 0, 0.87);
      cursor: initial;
      font-style: inherit;
      font-variant: inherit;
      font-weight: inherit;
      font-stretch: inherit;
      font-size: inherit;
      line-height: inherit;
      font-family: inherit;
      height: 100%;
      box-sizing: border-box;
      margin-top: 14px;
      text-transform: lowercase;
    }
}
.inp_password_div {
    font-size: 22px;
    line-height: 24px;
    width: 100%;
    height: 72px;
    display: inline-block;
