    transition: height 200ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    .sex_id_lab {
      position: absolute;
      line-height: 22px;
      // top: 38px;
      top: 63px;
      left: -45px;
      transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
      z-index: 1;
      cursor: text;
      transform: scale(0.75) translate(0px, -28px);
      transform-origin: left top 0px;
      pointer-events: none;
      user-select: none;
      color: rgba(0, 0, 0, 0.5);
    }
    .gender {
      margin-top: 40px;
      margin-left: 1vh;
    }
}
}
</style>




