<template>
  <div class="login">
    <div>
      <form >
        <div class="inp_name_div">
          <label for="username_id" class="username_id_lab">
            账号
          </label>
          <input type="text" name="username"  required="required" class="inp_name inp" id="username_id" ref='user' v-model="userName" @focus="change_hr_name()" @blur="reset_hr_name()">  
          <div>
            <hr class="hr_nor">
            <hr class="hr_name hr_c" v-bind:style="[hrName?hr_name_current:'']">
          </div>
        </div>
        <div class="inp_password_div">
          <label for="password_id" class="password_id_lab">
            密码
          </label>
          <input type="password" name="password" class="inp_password inp" id="password_id" ref='pwd' v-model="pwd" @focus="change_hr_password()" @blur="reset_hr_password()">  
          <div>
            <hr class="hr_nor" >
            <hr class="hr_password hr_c" v-bind:style="[hrPassword?hr_password_current:'']">
          </div>
        </div>

        <!-- <router-link to="/register"><input type="button" value="注册" class="btn-info btn" ></router-link> -->
        
        <input type="button" value="登录" class="btn-info btn" @click="checkUser()">
      </form>
    </div>
    <hr>
      <!-- <router-link :to="{name:'content2'}">look</router-link> -->
      <router-view></router-view>
  </div>

</template>
<script>
import {mapState} from 'vuex'
import {setCookie} from '../assets/js/setcookie.js'
import axios from 'axios'
import crypto from 'crypto'
export default {
  computed:mapState({
      user_name:state => state.user_name
  }),
  data(){
      return {
        userName: '',
        pwd: '',
        // 辅助功能
        classbg:'bg',
        classFade:'',
        notice_info:'用戶名或者密碼錯誤或者不存在！',
        info_hide:'hide',
        direct:'direct',
        pl2:'pl2',

        //  用于表单下划线样式的变换
        hrName: false,
        hrPassword: false,
        hr_name_current:{
          transform: 'scaleX(1)'
        },
        hr_password_current:{
          transform: 'scaleX(1)'
        }
        //  用于表单下划线样式的变换
      }
  },
   methods:{
    //  用于表单下划线样式的变换
     change_hr_name() {
       this.hrName = true;
     },
     change_hr_password() {
       this.hrPassword = true;
     },
     reset_hr_name:function() {
      this.hrName = false;
     },
     reset_hr_password:function() {
       this.hrPassword = false;
     },
     //  用于表单下划线样式的变换
    
    //  getmd5(code){
    //         return crypto.createHash('md5').update(code).digest('hex');
    //   },
     closemodel: function() {
                this.classFade = 'hide'
            },
     checkUser() {
        var name = this.userName;
        var pwd = crypto.createHash('md5').update(this.pwd).digest('hex');
        console.log(name+":"+pwd);
        var self = this; //很关键
        if(name != '' && pwd != '') {
          axios({
              method: 'post',
              url: 'user/searchUser',
              data: {
                username: name,
                pwd: pwd
						},
						timeout: 3000
                    }).then(function(response) {
                        // console.log(response.status)
                        var i, flag;
                        // for(i in response.data) {
                        // if((name == response.data[0].UserName && pwd == response.data[i].PassWord && name != '' && pwd != '')) {
                          console.log(response.data);
                          console.log(response.data[0]);
                          if(response.data==200){
                                flag = 'allright';
                            }
                        // }
                        if(flag == 'allright') {
                            var cookie_name = unescape(setCookie.setInfo().name);
                            if((cookie_name != name && name != '' && cookie_name != '') || cookie_name == '') {
                                setCookie.getInfo(self.userName, pwd);
                                setCookie.userLogin();
                                // self.classFade = '';
                                // self.info_hide = '';
                                // self.notice_info = '登錄成功！'
                                self.$router.push('/testpage')
                            } else if(cookie_name == name && name != '' && cookie_name != '') {
                                // self.classFade = '';
                                // self.info_hide = '';
                                // self.notice_info = '登錄成功！'
                                self.$router.push('/testpage')
                            }
                        } else {
                            // self.classFade = ''
                            // self.$router.push('/testpage')
                            alert("密码不正确");
                        }
                    }).catch(function(error) {
                        console.log(error);
                        // self.classFade = '';
                        // self.notice_info = '服務器繁忙，請刷新頁面或者稍後重試!(Error code: 504)'
                    });
                }
              else{
                alert("账号密码不能为空！");
                  // self.classFade = ''
              }
            }
  },
}
</script>
<style lang="scss" scoped>
.hr_nor {
  border-top: none rgb(224, 224, 224); 
  border-right: none rgb(224, 224, 224); 
  border-bottom: 1px solid rgb(224, 224, 224); 
  border-left: none rgb(224, 224, 224); 
  border-image: initial; 
  bottom: 8px; 
  box-sizing: content-box; 
  margin: 0px; 
  position: absolute; 
  width: 100%;
}
.hr_c {
      border-top: none rgb(0, 188, 212); 
      border-right: none rgb(0, 188, 212); 
      border-bottom: 2px solid rgb(0, 188, 212); 
      border-left: none rgb(0, 188, 212); 
      border-image: initial; 
      bottom: 8px; 
      box-sizing: content-box; 
      margin: 0px; 
      position: absolute; 
      width: 100%; 
      transform: scaleX(0); 
      transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    }
.login {
  width:100%;
  margin-top: 10vh;
  // background: url('../assets/image/bg.jpg') no-repeat 0 0;
  // background-size: cover;
  
}
form {
  width: 30%;
  margin: 0 auto;
.inp_name_div {
    font-size: 22px;
    line-height: 24px;
    width: 100%;
    height: 72px;
    display: inline-block;
    position: relative;
    background-color: transparent;
    font-family: Roboto, sans-serif;
    transition: height 200ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    .username_id_lab {
      position: absolute;
      line-height: 22px;
      // top: 38px;
      top: 63px;
      left: -45px;
      transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
      z-index: 1;
      cursor: text;
      transform: scale(0.75) translate(0px, -28px);
      transform-origin: left top 0px;
      pointer-events: none;
      user-select: none;
      color: rgba(0, 0, 0, 0.5);
    }
    .inp_name {
      -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
      padding: 0px;
      position: relative;
      width: 100%;
      border: none;
      outline: none;
      background-color: rgba(0, 0, 0, 0);
      color: rgba(0, 0, 0, 0.87);
      cursor: initial;
      font-style: inherit;
      font-variant: inherit;
      font-weight: inherit;
      font-stretch: inherit;
      font-size: inherit;
      line-height: inherit;
      font-family: inherit;
      height: 100%;
      box-sizing: border-box;
      margin-top: 14px;
      text-transform: lowercase;
    }
}
.inp_password_div {
    font-size: 22px;
    line-height: 24px;
    width: 100%;
    height: 72px;
    display: inline-block;
    position: relative;
    background-color: transparent;
    font-family: Roboto, sans-serif;
    transition: height 200ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    .password_id_lab {
      position: absolute;
      line-height: 22px;
      // top: 38px;
      top: 63px;
      left: -45px;
      transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
      z-index: 1;
      cursor: text;
      transform: scale(0.75) translate(0px, -28px);
      transform-origin: left top 0px;
      pointer-events: none;
      user-select: none;
      color: rgba(0, 0, 0, 0.5);
    }
    .inp_password {
      -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
      padding: 0px;
      position: relative;
      width: 100%;
      border: none;
      outline: none;
      background-color: rgba(0, 0, 0, 0);
      color: rgba(0, 0, 0, 0.87);
      cursor: initial;
      font-style: inherit;
      font-variant: inherit;
      font-weight: inherit;
      font-stretch: inherit;
      font-size: inherit;
      line-height: inherit;
      font-family: inherit;
      height: 100%;
      box-sizing: border-box;
      margin-top: 14px;
      text-transform: lowercase;
    }
}
.inp_sex_div {
    font-size: 22px;
    line-height: 24px;
    width: 100%;
    height: 72px;
    display: inline-block;
    position: relative;
    background-color: transparent;
    font-family: Roboto, sans-serif;
    transition: height 200ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    .sex_id_lab {
      position: absolute;
      line-height: 22px;
      // top: 38px;
      top: 63px;
      left: -45px;
      transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
      z-index: 1;
      cursor: text;
      transform: scale(0.75) translate(0px, -28px);
      transform-origin: left top 0px;
      pointer-events: none;
      user-select: none;
      color: rgba(0, 0, 0, 0.5);
    }
    .gender {
      margin-top: 40px;
      margin-left: 1vh;
    }
}
}
</style>




