<template>
  <div>
    <div class="container-fluid footer"> 
          <div class="row">
            <div class="col-lg-2 col-lg-offset-5">
              <h3 class="h_word">联系我们</h3>
            </div>
          </div>
          <div class="row">
            <div class="col-lg-4 col-lg-offset-4 col-md-4 col-md-offset-4">
              <img src="../assets/image/3dprinter/wechat.png" alt="" class="img-responsive center-block f_img">
              <img src="../assets/image/3dprinter/qq.png" alt="" class="img-responsive center-block f_img">
              <img src="../assets/image/3dprinter/weibo.png" alt="" class="img-responsive center-block f_img">
              <img src="../assets/image/3dprinter/facebook.png" alt="" class="img-responsive center-block f_img">
            </div>
          </div>
          <div class="row">
            <div class="col-lg-6 col-lg-offset-3 col-md-6 col-md-offset-3">
              <h3>地址：广东省广州大学城广东工业大学E110</h3>
            </div>
          </div>
          <div class="row">
            <div class="col-lg-6 col-lg-offset-3 col-md-6 col-md-offset-3">
              <h3>@2018-2019 Printors.Allright reserved.</h3>
            </div>
          </div>
      </div>

  </div>
</template>
<script>
import $ from 'jquery'
// $(function(){  
//         autoNav();  
//     });  
  
    //解决底部自动导航的问题  
    function autoNav(){  
        //获取内容的高度  
        var bodyHeight = $("body").height();  
        //获取底部导航的高度  
        var navHeight = $(".navbar").height();  
        //获取显示屏的高度  
        var iHeight = document.documentElement.clientHeight||document.body.clientHeight;  
        //如果内容的高度大于（窗口的高度 - 导航的高度）,z则需要添加一个div，设置其高度  
        if(bodyHeight > (iHeight - navHeight)){  
            $("body").append('<div style="height: '+navHeight+'px"></div>');  
        }  
    }  
export default {
  data(){
      return {
      }
  }
}
</script>

<style scoped lang="scss" > 

.footer {
   background:  #808080a1;
    div {
        margin-bottom: 10px;
        div {
            .f_img {
                width: 8%;
                display: inline-block;
                margin-left: 13.6%;
            }
            h3 {
                color:white;
                text-align: center;
            }
        }
    }
}

</style>


