<template>
  <div class="content">
    <div>
      <form >
        <h3 class="userinfo_title">
          用户信息
        </h3>
        <div class="inp_name_div">
          <label for="username_id" class="username_id_lab">
            账号
          </label>
          <input type="button" name="username"  required="required" class="inp_name inp" id="username_id" ref='user' v-model="author" >  
          <div>
            <hr class="hr_nor">
            <!-- <hr class="hr_name hr_c" v-bind:style="[hrName?hr_name_current:'']"> -->
          </div>
        </div>
        <div class="inp_password_div">
          <label for="password_id" class="password_id_lab">
            姓名
          </label>
          <input type="button" name="password" class="inp_password inp" id="password_id" ref='pwd' v-model="name" >  
          <div>
            <hr class="hr_nor" >
            <!-- <hr class="hr_password hr_c" v-bind:style="[hrPassword?hr_password_current:'']"> -->
          </div>
        </div>
        
        <!-- <router-link to="/register"><input type="button" value="注册" class="btn-info btn" ></router-link> -->
        
        <!-- <input type="button" value="修改密码" class="btn-info btn" @click="checkUser()"> -->
        <input type="button" value="修改密码" class="btn-info btn" @click="changepswpage()">
      </form>
    </div>
  </div>
</template>
<script>
   import {setCookie} from '../assets/js/setcookie.js'
   import {gettime} from '../assets/js/getTime.js'
   import axios from 'axios'
//通过表单上传数据，
//然后后台验证密码，
//产生返回数据，数据中包含是那台打印机，哪个用户，这个意义对前端意义不大，但是在后台中得用来产生表或者是创建表来生成数据
//密码回来后，返回标志域，
export default {
  data(){
      return {
        author:'',
        phonenum:'',
        name:'',
        sex:'',
      }
  },
  created() {
    this.author= unescape(setCookie.setInfo().name);
    var author_info = this.author;
    var currentdate = gettime.gettimer();
    var self  = this;
    if(this.author == ''){
      // this.errinfo = '未登錄，請登錄！';
      alert("未登陆，请登录");
      self.$router.push('/');
    }else {
      // alert("登陆成功，欢迎"+this.author);
    }
    axios({
      method: 'post',
      url: 'user/getUser',
      data: {
        username: this.author,
        // pwd: pwd
    },
    timeout: 3000
            }).then(function(response) {
                // console.log(response.status)
                var i, flag;
                // for(i in response.data) {
                // if((name == response.data[0].UserName && pwd == response.data[i].PassWord && name != '' && pwd != '')) {
                  // console.log(response.data);
                  // console.log(response.data[0]);
                  self.name= response.data[0].U_Name;
                  // if(response.data==200){
                  //       flag = 'allright';
                  //   }
                
            }).catch(function(error) {
                console.log(error);
                // self.classFade = '';
                // self.notice_info = '服務器繁忙，請刷新頁面或者稍後重試!(Error code: 504)'
            });
  },
  methods: {
    changepswpage() {
      var self = this;
      self.$router.push('/changepswpage')
    }
  }
  
}
</script>
<style scoped lang="scss" > 
.userinfo_title {
    color: gray;
    font-size: 2.1em;
    font-weight: 600;
    position: relative;
    left: -46px;
}
.content {
  margin-top: 10vh;
}
.hr_nor {
  border-top: none rgb(224, 224, 224); 
  border-right: none rgb(224, 224, 224); 
  border-bottom: 1px solid rgb(224, 224, 224); 
  border-left: none rgb(224, 224, 224); 
  border-image: initial; 
  bottom: 8px; 
  box-sizing: content-box; 
  margin: 0px; 
  position: absolute; 
  width: 100%;
}
.hr_c {
      border-top: none rgb(0, 188, 212); 
      border-right: none rgb(0, 188, 212); 
      border-bottom: 2px solid rgb(0, 188, 212); 
      border-left: none rgb(0, 188, 212); 
      border-image: initial; 
      bottom: 8px; 
      box-sizing: content-box; 
      margin: 0px; 
      position: absolute; 
      width: 100%; 
      transform: scaleX(0); 
      transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    }
.login {
  width:100%;
  margin-top: 10vh;
  // background: url('../assets/image/bg.jpg') no-repeat 0 0;
  // background-size: cover;
  
}
form {
  width: 30%;
  margin: 0 auto;
.inp_name_div {
    font-size: 22px;
    line-height: 24px;
    width: 100%;
    height: 72px;
    display: inline-block;
    position: relative;
    background-color: transparent;
    font-family: Roboto, sans-serif;
    transition: height 200ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    .username_id_lab {
      position: absolute;
      line-height: 22px;
      // top: 38px;
      top: 63px;
      left: -45px;
      transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
      z-index: 1;
      cursor: text;
      transform: scale(0.75) translate(0px, -28px);
      transform-origin: left top 0px;
      pointer-events: none;
      user-select: none;
      color: rgba(0, 0, 0, 0.5);
    }
    .inp_name {
      -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
      padding: 0px;
      position: relative;
      width: 100%;
      border: none;
      outline: none;
      background-color: rgba(0, 0, 0, 0);
      color: rgba(0, 0, 0, 0.87);
      cursor: initial;
      font-style: inherit;
      font-variant: inherit;
      font-weight: inherit;
      font-stretch: inherit;
      font-size: inherit;
      line-height: inherit;
      font-family: inherit;
      height: 100%;
      box-sizing: border-box;
      margin-top: 14px;
      text-transform: lowercase;
    }
}
.inp_password_div {
    font-size: 22px;
    line-height: 24px;
    width: 100%;
    height: 72px;
    display: inline-block;
    position: relative;
    background-color: transparent;
    font-family: Roboto, sans-serif;
    transition: height 200ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    .password_id_lab {
      position: absolute;
      line-height: 22px;
      // top: 38px;
      top: 63px;
      left: -45px;
      transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
      z-index: 1;
      cursor: text;
      transform: scale(0.75) translate(0px, -28px);
      transform-origin: left top 0px;
      pointer-events: none;
      user-select: none;
      color: rgba(0, 0, 0, 0.5);
    }
    .inp_password {
      -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
      padding: 0px;
      position: relative;
      width: 100%;
      border: none;
      outline: none;
      background-color: rgba(0, 0, 0, 0);
      color: rgba(0, 0, 0, 0.87);
      cursor: initial;
      font-style: inherit;
      font-variant: inherit;
      font-weight: inherit;
      font-stretch: inherit;
      font-size: inherit;
      line-height: inherit;
      font-family: inherit;
      height: 100%;
      box-sizing: border-box;
      margin-top: 14px;
      text-transform: lowercase;
    }
}
.inp_sex_div {
    font-size: 22px;
    line-height: 24px;
    width: 100%;
    height: 72px;
    display: inline-block;
    position: relative;
    background-color: transparent;
    font-family: Roboto, sans-serif;
    transition: height 200ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    .sex_id_lab {
      position: absolute;
      line-height: 22px;
      // top: 38px;
      top: 63px;
      left: -45px;
      transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
      z-index: 1;
      cursor: text;
      transform: scale(0.75) translate(0px, -28px);
      transform-origin: left top 0px;
      pointer-events: none;
      user-select: none;
      color: rgba(0, 0, 0, 0.5);
    }
    .gender {
      margin-top: 40px;
      margin-left: 1vh;
    }
}
}
</style>




